<template>
  <div class="gdt">
    <img
      src="http://wm-front-common.oss-cn-beijing.aliyuncs.com/wm-front-landing-page-mobile-custom/assets/KsPage/header.png"
      alt=""
      class="part1" />
    <div class="part6">
      <div class="tuikuan">- 开课3天内，对课程不满意无条件退还12元 -</div>
      <VanForm
        @submit="onSubmit"
        @failed="showErrorMsg"
        ref="form"
        validate-first
        :show-error="false"
        :show-error-message="false">
        <VanField
          v-model="form.phone"
          name="phone"
          placeholder="请输入手机号"
          :rules="[
            { required: true, message: '33003' },
            {
              pattern: phoneReg,
              message: '33004',
            },
          ]">
          <template #left-icon>
            <span class="prefix"> +86 <span class="icon">▼</span> </span>
          </template>
        </VanField>
        <VanField
          v-model="form.code"
          name="code"
          placeholder="请输入验证码"
          :rules="[{ required: true, message: '33005' }]">
          <template #button>
            <div class="code">
              <CountDown :before-send="beforeSend"></CountDown>
            </div>
          </template>
        </VanField>
        <p class="private" @click="stateModelShow = true">
          <span class="tips">提交即视为您已阅读并同意</span>《个人信息保护声明》
        </p>
        <div class="btn" @click="submit">
          <div class="submit">立即报名</div>
        </div>
      </VanForm>
    </div>
    <div class="zero__line-title">最新报名客户</div>
    <VanSwipe
      class="zero__swipe-box"
      :autoplay="3000"
      :show-indicators="false"
      vertical>
      <VanSwipeItem v-for="(item, index) in swipeList" :key="index">
        <div
          v-for="(el, index) in item"
          :key="index"
          class="zero__swipe-box__item">
          <p>{{ el.phone }}</p>
          <p>{{ el.state }}</p>
          <p>{{ el.time }}</p>
        </div>
      </VanSwipeItem>
    </VanSwipe>
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/9263phpj72vBH1600059012200914.png"
      alt=""
      class="part1" />
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/5430phpR84Acv1600059039200914.png"
      alt=""
      class="part1" />
    <img
      src="@/assets/KsPage/detail-why.png"
      alt=""
      class="part1" />
    <img
      src="https://oss-market.weimiaocaishang.com/Public/infoflow-admin/9573phpwPBTaT1600059045200914.png"
      alt=""
      class="part1" />
    <div class="right">
      <div class="name">北京微淼财商科技有限公司</div>
      <div class="num">京ICP备19026951号</div>
    </div>
    <div class="footer">
      <div>
        <p style="text-align: center; font-size: 16px; font-weight: bold;">
          仅售
          <span
            style="font-size: 22px; color: #ff2c12; font-weight: bold;">12元</span>
        </p>
      </div>
      <a class="baoming" @click="submit">立即报名</a>
    </div>
    <BackDiaLog
      @confirm="pay"
      @cancel="stop"
      v-model="showDialog" />
    <PayTypeDialog @change="pay" v-model="showPaySelet"></PayTypeDialog>
    <Statement v-if="stateModelShow" @onCloseMask="stateModelShow = false" />
  </div>
</template>
<script>
import { getSearch } from '@/utils'
import BackDiaLog from '@/components/common/StayDialog' // 支付挽留弹框
import PayTypeDialog from '@/components/common/SelectPayType' // 支付方式选择弹窗
import CountDown from '@/components/common/CountDown'
import { codeMap, swipeList } from './ErrorMsg'
import Statement from './components/Statement'
export default {
  components: {
    BackDiaLog,
    PayTypeDialog,
    CountDown,
    Statement
  },
  data: function () {
    return {
      swipeList,
      form: {},
      isFinsh: true,
      showPaySelet: false,
      showDialog: false,
      stateModelShow: false
    }
  },
  created () {
    this.form = {
      phone: localStorage.getItem('KsPage-phone'),
      code: localStorage.getItem('KsPage-code')
    }
    const needCheckOrder = getSearch('checkOrder')
    // 如果是支付返回的 则检查订单
    if (needCheckOrder) {
      this.changeQuery('checkOrder')
      this.showDialog = true
      this.checkOrder()
      this.timer = setInterval(() => {
        this.checkOrder()
      }, 3000)
    }
  },

  beforeDestroy () {
    clearInterval(this.timer)
  },

  methods: {
    stop () {
      clearInterval(this.timer)
    },
    // 提交表单
    submit () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_zsy_0346'
      })
      this.$refs.form.submit()
    },
    // 弹出提示
    showMsg (err) {
      const { msg, code } = err
      let confirmButtonText = '确定'
      if (err.code === 30006) {
        confirmButtonText = '查看分班'
      }
      this.$dialog({ message: msg || codeMap[code].msg, confirmButtonText }).then((res) => {
        if (code === 30006) {
          this.goSucccess(err)
        }
        this.$log.click(codeMap[err.code].report)
      })
    },
    // 跳转成功页面
    goSucccess (err) {
      localStorage.setItem('guide_phone', this.form.phone)
      localStorage.setItem('guide_order_num', err.data.order_num)
      this.$router.push({
        name: 'Officals',
        query: {
          ...this.$route.query
        }
      })
    },
    // 查询订单
    checkOrder () {
      if (!this.form.phone) return
      this.$apis.payment
        .checkOrder({
          phone: this.form.phone,
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .catch((err) => {
          // 支付成功后
          const code = err.code
          clearInterval(this.timer)
          this.showDialog = false
          if (code === 30006) {
            return this.goSucccess(err)
          }
          return this.showMsg(err)
        })
    },
    // 获取验证码
    beforeSend (start) {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_zsy_0345'
      })
      this.$refs.form
        .validate('phone')
        .then((res) => {
          start()
          this.$apis.userOperator.getPhoneCode({ phone: this.form.phone })
        })
        .catch((err) => {
          err = { code: err.message }
          this.showMsg(err)
        })
    },
    // 表单验证错误
    showErrorMsg (err) {
      err = { code: err.errors[0].message }
      this.showMsg(err)
    },
    // 表单校验失败
    onSubmit (e) {
      // 校验手机号和验证码
      localStorage.setItem('KsPage-phone', this.form.phone)
      localStorage.setItem('KsPage-code', this.form.code)
      console.log(this.form)
      this.$apis.payment
        .payBeforeCheck(this.form)
        .then(async (res) => {
          this.showPaySelet = true
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 支付
    pay (payType) {
      if (payType) {
        localStorage.setItem('payType', payType)
      }
      payType = localStorage.getItem('payType')
      const query = getSearch()
      const url = location.href + '&checkOrder=yes'
      this.$apis.payment
        .pay({
          ...query,
          ...this.form,
          pay_type: payType,
          return_url: encodeURIComponent(url),
          product_sn: 'PRODUCT_SN_XIAOBAI_12'
        })
        .then((data) => {
          if (payType === '1') {
            this.aliPay(data)
          }
          if (payType === '2') {
            this.wxPay(data)
          }
        })
        .catch((err) => {
          this.showMsg(err)
        })
    },
    // 微信支付
    wxPay (data) {
      window.location.replace(decodeURIComponent(data.url))
    },
    // 支付宝支付
    aliPay (data) {
      this.changeQuery('checkOrder', 'yes')
      const isReSet = document.getElementById('aLiPay')
      if (isReSet) {
        isReSet.parentNode.removeChild(isReSet)
      }
      const div = document.createElement('div')
      div.id = 'aLiPay'
      div.innerHTML = data
      document.body.appendChild(div)
      div.children[0].submit()
    },
    // 修改参数
    changeQuery (name, value) {
      const query = JSON.parse(JSON.stringify(this.$route.query))
      if (value) {
        query[name] = value
      } else {
        delete query[name]
      }
      this.$router.replace({
        ...this.$route,
        query: {
          ...query
        }
      })
    }

  }
}
</script>

<style lang="scss" scoped>
.gdt {
  ::placeholder {
    color: #5a98ff;
    font-weight: 400;
  }

  padding-bottom: 106px;

  .private {
    font-size: 24px;
    color: #609af9;
    line-height: 32px;
    text-align: center;
    box-sizing: border-box;
    margin: 30px 0 30px 0;
    .tips{
      color: #999;
    }
  }

  .right {
    text-align: center;
    padding: 15px;

    div {
      margin-bottom: 20px;
    }
  }

  .zero__line-title {
    margin-top: 25px;
    font-size: 30px;
    font-weight: 500;
    color: #38465d;
    text-align: center;
  }

  .zero__swipe-box {
    margin-left: 72px;
    height: 100px;
    margin-top: 30px;

    .zero__swipe-box__item {
      width: calc(100% - 100px);
      display: flex;
      justify-content: space-between;
      font-size: 26px;
      font-weight: 400;
      line-height: 50px;
      color: #434c5d;
    }
  }

  .footer {
    display: flex;
    position: fixed;
    bottom: 0;
    width: 100%;
    height: 106px;

    p {
      line-height: 106px;
    }
  }

  .baoming {
    display: block;
    font-family: PingFangSC-Regular;
    font-size: 23px;
    color: #fff;
    line-height: 0.84rem;
    margin: 0 auto;
    text-align: center;
    text-decoration: none;
    background: -webkit-linear-gradient(top, #ff929d, #ff0034);
    background: -o-linear-gradient(bottom, #ff929d, #ff0034);
    background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
    background: linear-gradient(to bottom, #ff929d, #ff0034);
    margin-bottom: 0.4rem;
  }

  .footer div {
    width: 40%;
    height: 100%;
    background-color: #fff;
    box-sizing: border-box;
  }

  .minge,
  .person {
    font-family: PingFangSC-Regular;
    font-size: 0.24rem;
    color: #333;
    text-align: center;
  }

  .person span {
    font-family: PingFangSC-Semibold;
    font-size: 0.4rem;
    color: #ff2c12;
    text-align: center;
  }

  .footer a {
    /* float: right; */
    display: block;
    width: 60%;
    height: 100%;
    font-family: PingFangSC-Semibold;
    font-size: 46px;
    color: #fffcff;
    line-height: 106px;
    text-align: center;
    text-decoration: none;
    cursor: pointer;
    background: -webkit-linear-gradient(top, #ff929d, #ff0034);
    background: -o-linear-gradient(bottom, #ff929d, #ff0034);
    background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
    background: linear-gradient(to bottom, #ff929d, #ff0034);
  }

  img {
    width: 100%;
    vertical-align: top;
  }

  .part6 {
    // height: 380px;
    box-sizing: border-box;
    padding: 20px 75px;
    text-align: center;

    .tuikuan {
      font-family: PingFangSC-Medium;
      font-size: 26px;
      color: #5a98ff;
      margin-top: 38px;
      margin-bottom: 38px;
      text-align: center;
    }

    .wrap {
      position: relative;
    }

    .btn {
      .submit {
        width: 600px;
        height: 80px;
        background: -webkit-linear-gradient(top, #ff929d, #ff0034);
        background: -o-linear-gradient(bottom, #ff929d, #ff0034);
        background: -moz-linear-gradient(bottom, #ff929d, #ff0034);
        background: linear-gradient(to bottom, #ff929d, #ff0034);
        border-radius: 3px;
        font-size: 40px;
        font-family: Source Han Sans CN;
        color: #fff;
        line-height: 80px;
      }
    }

    .code {
      display: inline-block;
      width: 229px;
      height: 100%;
      text-align: center;
      // border-left: rgba(0, 0, 0, 0.2) solid 1px;
      font-size: 32px;
      font-family: Source Han Sans CN;
      font-weight: 500;
      color: #eb3f3c;
      line-height: 32px;
      font-family: PingFangSC-Regular;
    }

    /deep/ .van-cell {
      .prefix {
        font-size: 32px;
        font-weight: 500;
        color: #000;
        display: flex;
        align-items: center;

        .icon {
          font-size: 25px;
          margin-left: 10px;
          color: #3c81fd;
        }

        padding-right: 15px;
      }

      margin-bottom: 20px;
      width: 600px;
      height: 86px;
      background: #f5f8ff;
      border-radius: 3px;
      font-size: 30px;
      font-family: SourceHanSansSC;
      font-weight: 500;
      color: #999;

      &::after {
        display: none;
      }
    }
  }
}
</style>
